    switch (level) {
      case '严重':
        return '#E56363'
      case '高危':
        return '#F49E0B'
      case '中危':
        return '#2F90EA'
      case '低危':
        return '#ABB2C0'
      case '提示':
        return '#DBDBDB'
    }
  }

